}

.nav-mobile {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding-top: 130px;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  transition: 0.6s;
}

.nav-mobile::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  width: 50%;
  background-color: rgba(73, 133, 255, 0.9);
  transform: translate(-100%, 0);
  transition: 0.6s;
}

.nav-mobile::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  width: 50%;
  background-color: rgba(73, 133, 255, 0.9);
  transform: translate(100%, 0);
  transition: 0.6s;
}

.nav-mobile__link {
  display: inline-block;
  padding: 15px 0;
  width: 100%;
  font-size: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  transform: translate(100%, 0);
}

.nav-mobile__link:hover {
  background-color: #3f7eff;
}

.nav-mobile_active {
  visibility: visible;
}

.nav-mobile_active::before {
  transform: translate(0, 0);
}

.nav-mobile_active::after {
  transform: translate(0, 0);
}

.nav-mobile_active .nav-mobile__link {
  transform: translate(0, 0);
  transition-duration: 1s;
}

.nav-mobile_active .nav-mobile__link:nth-child(1) {
  transition-delay: 0.1s;
}

.nav-mobile_active .nav-mobile__link:nth-child(2) {
  transition-delay: 0.2s;
}

.nav-mobile_active .nav-mobile__link:nth-child(3) {
  transition-delay: 0.3s;
}

.nav-mobile_active .nav-mobile__link:nth-child(4) {
  transition-delay: 0.4s;
}

.nav-mobile_active .nav-mobile__link:nth-child(5) {
  transition-delay: 0.5s;
}

.nav-mobile_active .nav-mobile__link:nth-child(6) {
  transition-delay: 0.6s;
}

.burger {
  position: fixed;
  right: 20px;
  top: 20px;
  z-index: 10;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: #f5f6f7;
  transition: ease 0.4s;
  cursor: pointer;
}

.burger__line {
  position: absolute;
  display: block;
  border-radius: 4px;
  padding: 0;
  width: 18px;
  height: 2px;
  background-color: #4985ff;
  transition: all 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
}

.burger__line:nth-child(1) {
  top: 14px;
}

.burger__line:nth-child(2) {
  transform: rotate(90deg);
}

.burger__line:nth-child(2),
.burger__line:nth-child(3) {
  left: 19px;
  top: 19px;
  width: 1px;
}

.burger__line:nth-child(3) {
  right: 0;
}

.burger__line:nth-child(4) {
  top: 19px;
  bottom: 0;
}

.burger__line:nth-child(5) {
  bottom: 14px;
}

.burger_active .burger__line:nth-child(1) {
  top: 19px;
  background-color: transparent;
}

.burger_active .burger__line:nth-child(2) {
  left: 10.5px;
  width: 18px;
  transform: rotate(45deg);
}

.burger_active .burger__line:nth-child(3) {
  left: 10.5px;
  width: 18px;
  transform: rotate(-45deg);
}

.burger_active .burger__line:nth-child(4) {
  background-color: transparent;
}

.burger_active .burger__line:nth-child(5) {
  bottom: 19px;
  background-color: transparent;
}

