  background-repeat: no-repeat;
}

.video__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.video__section-title {
  color: #fff;
}

.video__txt {
  margin-top: 9px;
}

.video-js {
  position: absolute;
  top: 220px;
}

.video-1-dimensions {
  border-radius: 10px;
  width: 763px;
  height: 429px;
}

.video-js .vjs-tech {
  border-radius: 10px;
}

.feedback {
  margin-top: 165px;
  padding-bottom: 81px;
  text-align: center;
}

.feedback__container {
  border-top: 1px solid #e0e0e0;
  padding-top: 84px;
}

.feedback__txt:last-child {
  margin-top: 32px;
  font-size: 13px;
  line-height: 21px;
}

.feedback__txt:nth-child(2) {
  margin-top: 10px;
}

.feedback__form {
  position: relative;
  margin-top: 22px;
}

.feedback__btn {
  border-radius: 0 30px 30px 0;
  width: 190px;
  box-shadow: 0 0 10px rgba(111, 111, 111, 0.25);
}

.field-email {
  border: 1px solid #f2f2f2;
  border-radius: 30px 0 0 30px;
  padding: 0 33px;
  width: 299px;
  height: 46px;
  box-shadow: 0 0 10px rgba(111, 111, 111, 0.25);
  box-sizing: border-box;
  line-height: 21px;
  background-color: #fff;
  transition: box-shadow 0.2s;
}

.field-email::placeholder {
  text-align: center;
  color: #999;
}

.field-email:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(73, 133, 255, 0.9);
}

.footer {
  padding: 68px 0 25px;
  background-color: #f6f6f8;
}

.footer__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 56px;
}

.footer__head {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
}

.footer__item {
  font-size: 14px;
  line-height: 22px;
}

.footer__item:not(:first-child) {
  margin-top: 25px;
}

.footer__link {
  transition: color 0.2s;
}

.footer__link:hover {
  color: #4985ff;
}

.footer__link:hover .footer__svg {
  --arrow-color: #4985ff;
}

.footer__svg {
  --arrow-color: #999;
  --icon-message-color: #999;
  --icon-call-color: #999;
  --icon-location-color: #999;
  margin-right: 10px;
  width: 15px;
  height: 15px;
  transition: 0.2s;
}

.footer__copyright {
  margin-top: 30px;
  font-size: 13px;
  line-height: 21px;
  text-align: center;
}

@media (max-width: 1284px) {
  .nav {
    margin-left: 0;
  }
}

@media (max-width: 1149px) {
  .specialization-item {
    align-items: center;
    text-align: center;
  }

  .portfolio__images {
    grid-template-columns: repeat(2, auto);
  }
}

@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  .container {
    padding: 0 15px;
  }

  .scroll-up {
    display: none;
  }

  .header {
    display: block;
    border-bottom: none;
  }

  .nav {
    display: none;
  }

  .burger {
    display: flex;
  }

  .hero__txt {
    margin: 20px 0 25px;
    font-size: 28px;
    line-height: 40px;
  }

  .btn {
    padding: 10px 0;
    width: 150px;
  }

  .section-title {
    font-size: 22px;
    line-height: 34px;
  }

  .services-item__icon::before {
    top: 4px;
    width: 50px;
    height: 50px;
  }

  .services-item__icon {
    width: 60px;
    height: 60px;
  }

  .services-item__svg {
    width: 20px;
    height: 20px;
  }

  .services-item {
    min-height: 120px;
  }

  .services {
    padding: 50px 0;
  }

  .aboutUs__content {
    position: relative;
    flex-direction: column;
    border-bottom: none;
    padding-bottom: 280px;
    text-align: center;
  }

  .aboutUs__img {
    position: absolute;
    top: 52%;
    border-radius: 0;
    width: 110%;
    height: 250px;
  }

  .aboutUs-item {
    max-width: 100px;
  }

  .aboutUs {
    padding: 50px 0;
  }

  .specialization__bg {
    height: 400px;
  }

  .specialization__head {
    flex-direction: column;
    padding-top: 50px;
    gap: 20px;
  }

  .specialization__items-swiper-wrap {
    display: block;
  }

  .specialization__items {
    display: none;
  }

  .specialization-item__img {
    width: 250px;
    height: 200px;
  }

  .specialization {
    padding-bottom: 50px;
    text-align: center;
  }

  .portfolio__images {
    display: none;
  }

  .portfolio__btn {
    margin: 13px auto 0;
  }

  .portfolio {
    padding: 50px 0;
  }

  .portfolio__images-swiper-wrap {
    display: block;
    margin-top: 20px;
  }

  .video {
    padding: 50px 0 165px;
  }

  .video-js {
    top: 160px;
  }

  .video-1-dimensions {
    width: 320px;
    height: 180px;
  }

  .feedback__container {
    padding-top: 40px;
  }

  .feedback__form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .feedback__btn {
    border-radius: 0 0 30px 30px;
    width: 80%;
  }

  .feedback {
    margin-top: 100px;
    padding-bottom: 50px;
  }

  .field-email {
    border-radius: 30px 30px 0 0;
    width: 80%;
    height: 40px;
  }

  .footer__container {
    justify-items: center;
    grid-template-columns: repeat(1, 1fr);
    padding-bottom: 30px;
    text-align: center;
    gap: 20px;
  }

  .footer__item:not(:first-child) {
    margin-top: 10px;
  }

  .footer__svg_arrow {
    display: none;
  }

  .footer {
    padding-top: 50px;
  }
}

@media (max-width: 423px) {
  .aboutUs__img {
    top: 57%;
  }

  .aboutUs__items {
    margin-top: 50px;
  }
}

@media print {
  .video-js > :not(.vjs-tech):not(.vjs-poster) {
    visibility: hidden;
  }
}